import React, { Component } from 'react';
import './index.css';
import ImageColumn from '../ImageColumn';

// Елементи з БД і їх обчислення
import _ from 'underscore';

class Images extends Component {  
  constructor(props) {
    super(props);
    this.props = props;
    this.state = {  dataUrl: [], show: false };
  };
  
  componentDidMount() {   
    if( this.props.urls.length > 0 ) {
      console.log( this.props.urls.length + ' > 0' );
      let ff = _.chunk( this.props.urls, this.props.urls.length / 4 );
      this.setState({ dataUrl: ff, show: true });
    };
  };

    render() {       
      const { dataUrl } = this.state;  
      // let dataUrl;
      // dataUrl = _.chunk(urlDB, urlDB.length / 4);
      // if(this.props.urls.images.length > 2) { dataUrl = _.chunk(this.props.urls.images, this.props.urls.images.length / 4);}
      // else { dataUrl = _.chunk(url, url.length / 4);}
      //console.log( this.props.urls.length );
      //console.log( 'New mass: ' + dataUrl.length );
      
      // const { filterURL, show } = this.state;                  
      return (        
        this.state.show ?
        <div>              
          <div className="boxPalette">
            <img className="palette" src="https://image.ibb.co/gqJr5J/v.jpg" alt="palette" />
          </div>
          
          <div className="row">
              <ImageColumn dataurl={ dataUrl[0] } />
              <ImageColumn dataurl={ dataUrl[1] } />
              <ImageColumn dataurl={ dataUrl[2] } />
              <ImageColumn dataurl={ dataUrl[3] } />         
          </div>
        </div>
        : <div>LOADING...</div>
      );
    }
  }
  
  export default Images;


