import React, { Component } from 'react';
import './index.css';

import Images from '../Images';

class All extends Component {
    constructor(props) {
        super(props);
        this.state = { url: [], show: false };
    };

    componentDidMount() {        
        fetch('/api/v1/portfolio/all')
        .then(response => {
            return response.json();
          }).then(data => {
            this.setState({ url: data.images, show: true });
          }).catch(function(err) {
            console.log(err)
          });     
    };

    render() {               
        return (
        <div>            
            <div>All</div>
            {this.state.show ? <Images urls={this.state.url} /> : null}
        </div>

        )        
    };
};

export default All;


